import React, { useState, useEffect} from "react";
import Header from "../Header";
import Grid from "@mui/material/Grid";
import Card from "react-bootstrap/Card";
import axios from "axios";
import { Button } from "react-bootstrap";
const National = () => {
  const [data, setdata] = useState([]);
  const path = "data:image/png;base64,";
  useEffect(() => {
    const api =
      "https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=bab472ef047b4470bbfa8a676b765823";
    axios
      .get(api)
      .then((response) => {
        setdata(response.data.articles);
      })
      .catch((error) => {
        console.warn(error);
      });
  }, []);
  return (
    <div>
      <Header />
      <h1 style={{textAlign: 'center',color: 'white',fontSize:'3rem' }}>Bussiness Today</h1>
      <div style={{ maxWidth: "auto", margin: "auto" }}>
        <Grid container spacing={4} justifyContent="center" alignItems="center">
          {data.map((item, i) => {
            return (
              <Grid item key={i}>
                <Card style={{ maxWidth: "28rem", height: "28rem" }}>
                  <Card.Img
                    variant="top"
                    src={item.urlToImage}
                    height={200}
                    width={200}
                    alt="Error"
                    onError={(e) => {
                      e.target.src ={path};
                    }}
                  />
                  <Card.Body>
                    <Card.Title>{item.title}</Card.Title>
                    <Card.Text>{item.description}</Card.Text>
                  </Card.Body>
                  <Button onClick={() => window.location.assign(item.url)}>
                    Know more
                  </Button>
                </Card>
              </Grid>
            );
          })}
        </Grid>
      </div>
    </div>
  );
};

export default National;
